






















































































































import relation from '../../components/relation.vue'

export default {
  components: {
    relation
  },
  data() {
    return {
      navLists: [
        {
          name: '直推团队',
          type: 1
        },
        {
          name: '拓展团队',
          type: 2
        }
      ],
      navIndex: 0,
      tabLists: {
        '1': [
          {
            name: '锁粉',
            title: '锁粉客户',
            role: 0
          },
          {
            name: '普通',
            title: '普通会员',
            role: 1
          },
          {
            name: '黄金',
            title: '黄金VIP',
            role: 2
          },
          {
            name: '铂金',
            title: '铂金VIP',
            role: 3
          },
          {
            name: '钻石',
            title: '钻石VIP',
            role: 4
          }
        ],
        '2': [
          {
            name: '黄金',
            title: '黄金VIP',
            role: 2
          },
          {
            name: '铂金',
            title: '铂金VIP',
            role: 3
          },
          {
            name: '钻石',
            title: '钻石VIP',
            role: 4
          }
        ]
      },
      activeId: 0, // 当前选中tab
      lists: [],
      listsLength: 0,
      sortLists: [
        {
          name: '时间从后到先',
          status: 3
        },
        {
          name: '时间从先到后',
          status: 4
        },
        {
          name: '收益从高到低',
          status: 5
        },
        {
          name: '收益从低到高',
          status: 6
        }
      ],
      sortStatus: 0,
      sortType: 3,
      showSort: false,
      currentPage: 1, // 当前页
      maxPage: 1, // 最大页
      loading: false, // 加载中
      searchStr: '', // 搜索字段
      searching: false, // 搜索状态
      inputing: false,
      wechatFlag: false,
      wechatStr: '',
      wechatQrCode: '',
      relationData: {}
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo
    },
    role() {
      return this.userInfo.role
    },
    isLoad() {
      return parseInt(this.maxPage) <= parseInt(this.currentPage) && this.lists.length > 0
    },
    tipText() {
      return '尚无' + this.tabLists[this.navLists[this.navIndex].type][this.activeId].title
    },
    defaultAvatar() {
      return '../../static/images/avatar.png'
    },
    showSearch() {
      return (this.lists.length > 0 && !this.loading) || this.searching
    },
    showNoRecord() {
      return this.lists.length <= 0 && !this.loading
    }
  },
  onLoad() {
    this.updateLists(this.navLists[this.navIndex].type, this.tabLists[this.navLists[this.navIndex].type][this.activeId].role)
  },
  methods: {
    navChange(n) {
      if (this.navIndex === n) return
      if (this.loading) {
        uni.showToast({
        	title: '正在加载数据',
          icon: 'none'
        })
        return
      }
      this.loading = false
      this.navIndex = n
      this.sortStatus = 0
      this.sortType = 3
      this.searchStr = ''
      this.searching = false
      this.lists.length = 0
      this.listsLength = 0
      this.currentPage = 1
      this.maxPage = 1
      this.activeId = 0
      this.updateLists(this.navLists[this.navIndex].type, this.tabLists[this.navLists[this.navIndex].type][this.activeId].role)
    },
    tabChange(n) {
      if (this.activeId === n) return
      if (this.loading) {
        uni.showToast({
        	title: '正在加载数据',
          icon: 'none'
        })
        return
      }
      this.loading = false
      this.activeId = n
      this.sortStatus = 0
      this.sortType = 3
      this.searchStr = ''
      this.searching = false
      this.currentPage = 1
      this.updateLists(this.navLists[this.navIndex].type, this.tabLists[this.navLists[this.navIndex].type][this.activeId].role)
    },
    updateLists(type, role) {
      if (this.loading) return
      this.loading = true
      if (parseInt(this.currentPage) <= 1) {
        this.lists.length = 0
        this.listsLength = 0
      }
      let sortType = this.sortType
      if (this.tabLists[this.navLists[this.navIndex].type][this.activeId].role === 0) {
        if (this.sortType === 3) {
          sortType = 1
        } else if (this.sortType === 4) {
          sortType = 2
        }
      }
      this.$http({
        url: this.$apis.myTeam,
        method: 'GET',
        data: {
          type: type,
          role: role,
          order: sortType,
          page: this.currentPage,
          keyword: this.searchStr
        },
        cb: (err, data) => {
          if (parseInt(this.currentPage) <= 1) {
            this.lists.length = 0
            this.listsLength = 0
          }
          if (!err && data.success) {
            this.listsLength = data.data.total
            this.currentPage = data.data.current_page
            this.maxPage = data.data.last_page
            let oldArr = this.lists
            let newArr = data.data.data
            this.lists = oldArr.concat(newArr)
            this.lists.forEach(item => {
              this.$set(item, 'show', false)
            })
            if (this.searchStr.length > 0) this.searching = true
            setTimeout(() => {
              this.loading = false
            }, 600)
          } else {
            this.searching = false
            this.loading = false
            uni.showToast({
            	title: '获取团队数据失败',
              icon: 'none'
            })
          }
        }
      })
    },
    showSortPop() {
      this.showSort = true
    },
    hideSortPop() {
      this.showSort = false
    },
    clickSort(n, type) {
      this.sortStatus = n
      this.hideSortPop()
      this.sortType = type
      this.searchStr = ''
      this.searching = false
      this.currentPage = 1
      this.updateLists(this.navLists[this.navIndex].type, this.tabLists[this.navLists[this.navIndex].type][this.activeId].role)
    },
    slideToggle(that) {
      this.lists.forEach(item => {
        item.show = item.id === that.id ? !item.show : false
      })
      if (!that.pregressArr) {
        this.$http({
          url: this.$apis.sxProgress,
          method: 'GET',
          data: {
            id: that.id
          },
          cb: (err, data) => {
            if (!err && data.success) {
              this.$set(that, 'pregressArr', data.data)
            } else {
              that.show = false
              uni.showToast({
              	title: '获取升级进度数据失败',
                icon: 'none'
              })
            }
          }
        })
      }
    },
    search() {
      if (this.searchStr.length <= 0) {
        uni.showToast({
        	title: '请输入搜索内容',
          icon: 'none'
        })
        return
      }
      this.currentPage = 1
      this.updateLists(this.navLists[this.navIndex].type, this.tabLists[this.navLists[this.navIndex].type][this.activeId].role)
    },
    resetSearch() {
      this.currentPage = 1
      this.searchStr = ''
      this.searching = false
      this.updateLists(this.navLists[this.navIndex].type, this.tabLists[this.navLists[this.navIndex].type][this.activeId].role)
    },
    scrollUpdate() {
      if (!this.loading && !this.isLoad && this.lists.length > 0) {
        this.currentPage ++
        this.updateLists(this.navLists[this.navIndex].type, this.tabLists[this.navLists[this.navIndex].type][this.activeId].role)
      }
    },
    showWechat(str, url) {
      if (!str && !url) return
      this.wechatStr = str
      this.wechatQrCode = url || ''
      this.wechatFlag = true
    },
    hideWechat() {
      this.wechatFlag = false
    },
    call(num) {
      if (!num) {
        uni.showToast({
        	title: '用户未填写手机号码',
          icon: 'none'
        })
      } else {
        uni.makePhoneCall({
        	phoneNumber: num
        })
      }
    },
    copy(str) {
      uni.setClipboardData({
      	data: str,
        success: () => {
          uni.showModal({
            title: '复制成功',
            content: '微信已复制，请打开微信查找添加',
            showCancel: false
          })
        },
        fail: () => {
          uni.showModal({
            title: '复制失败',
            content: str,
            showCancel: false
          })
        },
        complete: () => {
        	this.hideWechat()
        }
      })
    },
    showExplain(b) {
      if (this.navIndex === 0) {
        this.relationData = {
          show: b
        }
      } else {
        uni.showModal({
        	title: '拓展体系',
          content: '您的团队成员每人会贡献2个VIP进入您的拓展团队，平台将给予您5%的额外奖励。',
          showCancel: false,
          confirmText: '知道了'
        })
      }
    }
  }
}
